import * as React from 'react';
import { Helmet } from 'react-helmet';
import {
    NavLink
} from 'react-router-dom';

const Home = (props: any) => (
    <div>
        <Helmet>
            <title>Introduction | Cyprien Taque</title>
        </Helmet>
        <h2 className="regular purple">Dveloppement logiciel</h2>
        <p className="grey">
            Actuellement dveloppeur front-end chez <a rel="noopener noreferrer" target="_blank" href="https://www.cinedigitalservice.com/cds/"> Cin digital services</a>, je me suis initi au dveloppement web par la cration du site <a rel="noopener noreferrer" target="_blank" href="http://ctprods.fr"> CTProds</a>, puis j'ai complt ma formation autodidacte par une  formation diplomante d'un titre de dveloppeur logiciel  l'cole IMIE (nov. 2016 - sept. 2018).<br /><br />J'aime les architectures propres et bien penses, la programmation fonctionnelle et les interfaces design. <a rel="noopener noreferrer" target="_blank" href="https://ant.design/">#antd</a>
        </p>
        <br /><br />
        <h2 className="regular purple">Productique bois et conception 3d</h2>
        <p className="grey">
            Dans une autre vie... <br />
            Mon parcours de formation en <NavLink to="/studies/wood">productique bois </NavLink>suivi d'<NavLink to="/experiences/wood">expriences professionnelles</NavLink> dans ce domaine m'ont apport une double comptence <strong>manuelle</strong> en atelier de menuiserie et <b>intllectuelle</b> en conception dans l'industrie. <br /><br />
            J'ai xerc le mtier de technicien (dessinateur, chef de projets en recherche et dveloppement) dans les domaines de la menuiserie industrielle, l'agencement mobilier et la conception de salles de bains en matriaux composites et tle plie. Voici quelques-uns des projets que j'ai ralis dans ce domaine: <a rel="noopener noreferrer" target="_blank" href="http://ctprods.fr/profile/CyprienTaque"> CTProds</a>
        </p>
    </div>
);
export default Home;



